import React, { useState, useEffect } from "react";
import angel from "./img/angel-child2.png";
import demon from "./img/demon-child.png";
import twitterIcon from "./img/Twitter-icon2.png";
import tumblrIcon from "./img/tumblr-icon.png";


let random = Math.floor(Math.random() * 1643);

const App = () => {
    const [quotes, setQuotes] = useState([]);
    const [oneQuote, setOneQuote] = useState({});
    const [color, setColor] = useState("container light");
    const [move, setMove] = useState("");
    const [baby, setBaby] = useState(angel);
    
    
    let twitter = `https://twitter.com/intent/tweet?hashtags=quotes&related=freecodecamp&text='${oneQuote.text + oneQuote.author}'`
    let tumblr = `https://www.tumblr.com/widgets/share/tool?posttype=quote&tags=quotes,freecodecamp&caption='${oneQuote.text}&content='${oneQuote.author}'&canonicalUrl=https%3A%2F%2Fwww.tumblr.com%2Fbuttons&shareSource=tumblr_share_button'`

    useEffect(() => {
        function fetchQuotes() {fetch("https://type.fit/api/quotes")
            .then(function(response) {
                return response.json()
            })
            .then(function(data) {
                setQuotes(data)
                setOneQuote(data[random])
            })
        } fetchQuotes()
    }, [move])

    const nextQuote = () => {
        let random = Math.floor(Math.random() * 1643);
        setOneQuote(quotes[random])
    }

    const changeMode = () => {
        (move === 20) ? setMove(-2) : setMove(20);
        (baby === angel) ? setBaby(demon) : setBaby(angel);
        (color === "container light") ? setColor(`container dark`) : setColor("container light");
        console.log(move)
    }

    return(
        <div className={color}>
            <div id="quote-box" className="box">
                <header>
                    <h3><span>AB</span> quotes</h3>
                    <div className="filter" onClick={() => changeMode()}>
                        <span style={{ left: move }}></span>
                    </div>
                </header>
                <section>
                    <img src={baby} alt="angel"/>
                    <div className="para">
                        <div id="text">"{oneQuote.text}</div>
                        <div id="author">{oneQuote.author}</div>
                        <div className="social">
                            <a target="_top" id="tweet-quote" href={twitter} title="post on twitter"><img src={twitterIcon} alt="twitter"/></a>
                            <a href={tumblr} title="post on tumblr"><img src={tumblrIcon} alt="twitter"/></a>
                        </div>
                        <button id="new-quote" onClick={() => nextQuote()}>NEXT QUOTE</button>
                    </div>
                </section>
                <footer>

                </footer>
            </div>
        </div>
    )
}

export default App;